import { disablePageScroll, enablePageScroll, clearQueueScrollLocks, getScrollState } from 'scroll-lock';


// MENU

const navToggle = document.getElementById('nav-toggle');
const headerMenu = document.getElementById('header__menu');
const wrapper = document.querySelector('.wrapper');
const body = document.querySelector('body');
const lockPadding = document.querySelectorAll(".lock-padding");

let unlock = true;

// const timeout = 450;

function openMenu() {
	if (unlock) {
		navToggle.classList.toggle('active');
		headerMenu.classList.toggle('active');
		wrapper.classList.toggle('active');
		disablePageScroll(headerMenu);
		console.log(getScrollState())

		// if (headerMenu.classList.contains('active')) {
		// 	bodyLock();
		// } else {
		// 	bodyUnLock();
		// }
	}
}

function closeMenu(doUnlock = true) {
	if (unlock) {
		navToggle.classList.remove('active');
		headerMenu.classList.remove('active');
		wrapper.classList.remove('active');
		enablePageScroll(headerMenu);
		
		// if (doUnlock) {
		// 	bodyUnLock();
		// }
	}
}

// if (navToggle) {
//     navToggle.addEventListener('click', function(e) {
//         openMenu();
//     })
// }

navToggle.addEventListener('click', function(){
	navToggle.classList.toggle('active');
	headerMenu.classList.toggle('active');
	wrapper.classList.toggle('active');

	if(navToggle.matches('.active')) {
		disablePageScroll(headerMenu);
	} else {
		enablePageScroll(headerMenu);
	}
})

document.addEventListener('click', function(e) {
	if(!e.target.matches('.nav-toggle, .nav-toggle *, .header__menu, .header__menu *')) {
		closeMenu();
	}
})


